import React, { Component } from 'react'

import { Input, Button } from 'reactstrap'

class SearchAsinInput extends Component {
  constructor(props) {
    super(props)

    this.state = {
      asinValue: '',
    }
  }

  render() {
    const { direction = 'column', margin = 0 } = this.props

    return (
      <div style={{ display: 'flex', flexDirection: direction }}>
        <Input
          type="text"
          placeholder="ASIN NUMBER"
          onChange={this.props.onChange}
          value={this.props.asinValue}
          name={'asinValue'}
        />
        <div>
          <Button
            color="gray"
            style={{ marginTop: '15px', marginRight: '10px', marginLeft: margin, width: '68px' }}
            onClick={this.props.onSubmit}
          >
            {this.props.name}
          </Button>
          {this.props.deleteItem ? (
            <Button color="gray" style={{ marginTop: '15px' }} onClick={this.props.deleteItem}>
              Delete
            </Button>
          ) : null}
        </div>
      </div>
    )
  }
}

export default SearchAsinInput
