import React, { PureComponent } from 'react'
import { Row, Col } from 'reactstrap'
import { PieChart, Pie, Sector, Cell } from 'recharts'

import './revenuechart.css'

export default class RevenueChart extends PureComponent {
  render() {
    const staticdata = [
      { name: 'Group A', qty: 400 },
      { name: 'Group B', qty: 300 },
      { name: 'Group C', qty: 300 },
      { name: 'Group D', qty: 200 },
    ]

    const { data } = this.props

    //console.log('data in revenue chart', data)
    let newData = []
    if (data) {
      data.map(el => {
        newData.push({ qty: parseInt(el.qty) })
      })
    }
    //console.log('newData', newData)

    const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042']

    const RADIAN = Math.PI / 180
    return (
      <Row>
        <PieChart width={320} height={320}>
          <Pie
            data={newData}
            cx={120}
            cy={200}
            innerRadius={60}
            outerRadius={80}
            fill="#8884d8"
            paddingAngle={5}
            width={200}
            height={200}
            dataKey={'qty'}
          >
            {data
              ? data.map((entry, index) => (
                  <Cell key={index} fill={COLORS[index % COLORS.length]} />
                ))
              : ''}
          </Pie>
        </PieChart>
      </Row>
    )
  }
}
